@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap");
/*===== VARIABLES CSS =====*/
:root {
  /*===== Colores =====*/
  --first-color: #1a73e8;
  --input-color: #80868b;
  --border-color: #dadce0;

  /*===== Fuente y tipografia =====*/
  --body-font: "Roboto", sans-serif;
  --normal-font-size: 1rem;
  --small-font-size: 0.75rem;
}

/*===== BASE =====*/
*,
::before,
::after {
  box-sizing: border-box;
}

h1 {
  margin: 0;
}

/*===== FORM =====*/
.l-form {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  /* width: 100vh; */
  margin-left: 10px;

  /* background-image:linear-gradient( to top right,#b3ecff,pink);  */
}
.form {
  /* width: 300px; */
  padding: 1rem 1.1rem;
  border-radius: 3rem;
  /* margin: 145px; */
  /* box-shadow: 10px 10px 15px 1px rgba(61, 65, 68, 0.23); */
  box-shadow: 0 0 10px darkslategray;
  /* background-position: left; */
  /* background: #000; */
  background-image: linear-gradient(to top right, #b3ecff, pink);
}

.form-div {
  position: relative;
  height: 48px;
  margin-bottom: 1.5rem;
}
.form-log {
  position: relative;
  height: 48px;
  /* width: 300px; */
  margin-left: 34px;

  /* margin-bottom: 1.5rem; */
}
.form-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 320px;
  /* style={{width:'400px'}} */
  height: 0px;
  font-weight: 500;
  /* font-size: 10px; */
  border: 1px solid grey;
  border-radius: 0.5rem;
  outline: none;
  padding: 2rem;
  /* background: red; */
  z-index: 1;
}

.form-label {
  position: absolute;
  left: 1rem;
  top: 1rem;
  padding: 0 0.62rem;
  background-color: #f5f5f5;
  /* background-image: linear-gradient(0deg, white 70%, lavender 30%); */
  color: grey;
  font-size: 12px;
  font-weight: normal;
  transition: 0.5s;
}
.form-button {
  display: block;
  margin-left: auto;
  padding: 0.75rem 2rem;
  outline: none;
  border: none;
  background-color: black;
  color: #fff;
  /* font-size: var(--normal-font-size); */
  border-radius: 0.5rem;
  cursor: pointer;
  transition: 0.3s;
}
.form-button:hover {
  box-shadow: 0 10px 36px rgba(0, 0, 0, 0.15);
}

/*Input focus move up label*/

/*Input focus sticky top label*/
.form-input:not(:placeholder-shown).form-input:not(:focus) + .form-label {
  top: -0.5rem;
  left: 0.8rem;

  font-weight: 500;
  z-index: 10;
}
.form-control:not(:placeholder-shown).form-control:not(:focus) + .form-label {
  /* top: -.2rem;
   left: .8rem;
  
  font-weight: 500;
  z-index: 10; */

  top: -0.2rem;
  left: 0.8rem;
